* {
  box-sizing: border-box;
}
html {
  overflow-x: hidden;
  @media screen and (max-height: 768px){
    font-size: 95%;
  }
  @media screen and (max-width: $bp-tablet - 1px){
    font-size: 75%;
  }
}
html, body {
    background-color: $black;
    color: $whiteWhite;
    text-shadow: 0 0 10px rgba(0,0,0,.15);
    width: 100%;
    @include Aileron;

}

body {
  position: relative;
}

::selection {
    background: rgba($white, .05);
}

*:last-child {
    margin-bottom: 0;
}
