/*=============================================================================
# LANDING PAGE
==============================================================================*/
.panel--landing {
  overflow: hidden;
  height: 100vh;
  padding: 5rem;

  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    background: linear-gradient(transparentize($black, 0.15), $black);
    z-index: 5;
  }
}
#heroVideo {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  min-width: 100%;
  min-height: 100%;
  opacity: 1;
  z-index: 1;
}

#hero-logo {
  max-width: 600px;
  width: 33vw;
  min-width: 250px;
  display: block;
  margin: 0 auto;
  transform: translateX(-10%);
}

.landing-details {
    margin: 3rem 0;
    @include simpleAnimateIn(0.3s);

    &::after {
      content: '';
      display: block;
      clear: both;
    }
    .event-details {
      display: block;
        &-lg {
          font-weight: 500;
          font-size: 1.125rem;
          letter-spacing: 2px;
          display: block;
          margin-bottom: 2px;
        }
        &-sm {
          opacity: .7;
        }
    }

    .detailbox {
      float: left;
      margin-right: 4rem;
        img {
          width: 2.5rem;
          display: inline-block;
          margin-right: 20px;
          vertical-align: middle;
          opacity: .9;
        }
        @media (max-width: 1000px) {
          float: none;
          display: block;
          clear: both;
          margin-bottom: 1.5rem;
          padding-left: 5rem;
          position: relative;
          img {
            position: absolute;
            top: 0; bottom: 0; left: 0; margin: auto;
          }
        }
    }
    .detail-text {
      display: inline-block;
      vertical-align: middle;
    }
}

#main-content {
  [class^="paragraph"]{
    &:nth-of-type(1){
      @include simpleAnimateIn(0.4);
    }
    &:nth-of-type(2){
      @include simpleAnimateIn(0.5);
    }
  }

}


.strip-container {
  margin-top: 5vh;
  height: 75vh;
  width: 99%;
  display: inline-block;
  position: relative;
  vertical-align: middle;

  .single-grad {
    display: inline-block;
    width: 20%;
    margin: 3rem 0 0;
    list-style: none;

    .grad-container {
      display: block;
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
      background: $actualWhite;
      padding-top: 20px;
      font-size: 0;

      .grad-name {
        padding: 10px 0;
        display: block;
        width: 100%;
        text-align: center;
        position: absolute;
        top: 0;
        left: 0;
        color: $black;
        text-decoration: none;
        font-size: 1rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
        font-weight: 500;
        transform: translateY(-100%);
        transition: all .2s;
        opacity: .3;
      }

      &:hover {
        .grad-name {
          opacity: 1;
          transform: translateY(0);
        }
      }

      img {
        width: 100%;
        height: auto;
      }
    }
  }
}
